* {
  box-sizing: border-box;
}

body {
  margin: 0;
}

.conbox {
  display: flex;
}
.leftnav {
  background-color: rgb(240, 223, 76);
  height: 100vh;
  padding: 1rem;
}

.rightnav {
  padding: 1rem;
}

.site-title {
  font-size: 2rem;
  margin-bottom: 20px;
}

.nav ul {
  padding: 0;
  margin: 0;
  list-style: none;
  gap: 1rem;
}

.nav a {
  color: inherit;
  text-decoration: none;
  height: 100%;
  display: flex;
  align-items: center;
  padding: 10px;
}

.nav li.active {
  background-color: #555;
}

.nav li:hover {
  background-color: #777;
}
